import React from 'react';
import Header from './Header';
import Informations from './Informations';
import Contact from './Contact';
import TopBar from './TopBar';
import TopBarMobile from './TopBarMobile';
import Footer from './Footer';
import Localization from './Localization';
import Spinner from './Spinner';
import AOS from 'aos';

class App extends React.Component {

    state = { contentLoaded: false, width: 0, height: 0 };

    onLoad = () => {
        this.setState({contentLoaded: true});
        this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
        console.log('loaded!');
    }

    componentDidUpdate = () => {
        AOS.init({
            duration : 600
        })
    }

    componentDidMount = () => {
        this.updateWindowDimensions();
        window.addEventListener('resize', this.updateWindowDimensions);
      }
      
      componentWillUnmount = () => {
        window.removeEventListener('resize', this.updateWindowDimensions);
      }
      
      updateWindowDimensions = () => {
        this.setState({ width: window.innerWidth, height: window.innerHeight });
      }

    render() {
        return (
            <div>
                {this.state.contentLoaded ? null : 
                    <Spinner small={false}/>
                }
                <div style={this.state.contentLoaded ? {} : {display: 'none'}} className='content'>
                    {this.state.width >= 1100 ? <TopBar /> : <TopBarMobile /> }
                    <Header onLoad={this.onLoad}/>
                    <Informations />
                    <Localization />
                    <Contact />
                    <Footer />
                </div>
            </div>
        );
    };
}

export default App;