import React from 'react';
import Title from './Title';
import MapContainer from './MapContainer';

class Localization extends React.Component {

    render() {
        const markerChurch =  process.env.PUBLIC_URL + "/images/marker-church.png";
        const markerWedding =  process.env.PUBLIC_URL + "/images/marker-wedding.png";

        return (
            <div id='localization'>
                <Title text='Lokalizacja' />
                <div className='localization'>
                   <div className='left'>
                        <div className='localization-title'>Ślub</div>
                        <img className='marker' src={markerChurch}></img>
                        <div className='localization-info'>
                            <p>Kościół św. Tomasza Apostoła w Kietrzu</p>
                            <p>Plac Biskupa Konrada 3</p>
                        </div>
                   </div>
                   <div className='right'>
                        <div className='localization-title'>Wesele</div>
                        <img className='marker' src={markerWedding}></img>
                        <div className='localization-info'>
                            <p>Dom Weselny "Rogożany"</p>
                            <p>Rogożany 63</p>
                        </div>
                   </div>
                </div>
                <div className='map-container'>
                    <MapContainer />
                </div>
            </div>
        );
    };
}

export default Localization;