import React from 'react';
import {Map, InfoWindow, Marker, GoogleApiWrapper} from 'google-maps-react';
 
export class MapContainer extends React.Component {

    state = {
        showingInfoWindow: false,
        activeMarker: {},
        selectedPlace: {},
      };

    onMarkerClick = (props, marker, e) =>
    this.setState({
      selectedPlace: props,
      activeMarker: marker,
      showingInfoWindow: true
    });

    render() {
        const style = {
            width: '100%',
            maxWidth: '100%',
            height: '600px'
        }

        const markerChurch =  process.env.PUBLIC_URL + "/images/marker-church.png";
        const markerWedding =  process.env.PUBLIC_URL + "/images/marker-wedding.png";
        const markerSize = 80;
        return (
            <Map
                google={window.google}
                style={style}
                initialCenter={{
                lat: 50.092173,
                lng: 17.957561
                }}
                zoom={13}
                onClick={this.onMapClicked}
            >
                <Marker
                    title={'Wesele'}
                    name={'Dom Weselny \'Rogożany\''}
                    position={{lat: 50.092954, lng: 17.908553}} 
                    onClick={this.onMarkerClick}
                    icon={{
                        url: markerWedding,
                        anchor: new window.google.maps.Point(markerSize/2, markerSize),
                        scaledSize: new window.google.maps.Size(markerSize, markerSize)
                        }} 
                />
                <Marker
                    title={'Ślub'}
                    name={'Kościół św. Tomasza Apostoła w Kietrzu'}
                    position={{lat: 50.080402, lng: 18.000644}}
                    onClick={this.onMarkerClick}
                    icon={{
                        url: markerChurch,
                        anchor: new window.google.maps.Point(markerSize/2, markerSize),
                        scaledSize: new window.google.maps.Size(markerSize, markerSize)
                        }} 
                />
                <InfoWindow
                    marker={this.state.activeMarker}
                    visible={this.state.showingInfoWindow}>
                        <div style={{textAlign: 'center'}}>
                            <h1>{this.state.selectedPlace.title}</h1>
                            <h2>{this.state.selectedPlace.name}</h2>
                        </div>
                </InfoWindow>
                </Map>
                
        );
    }
}
 
export default GoogleApiWrapper({
  apiKey: 'AIzaSyCKfYcX2NExxNiQIC6HhFDwq-sPT9n6Of4'
})(MapContainer)